<script>
export default {
	data() {
		return {
			ovoUserSelf: {},
			vipList: [],
			selectedOfItem: null
		}
	},
	created() {
		// 从缓存中获取
		let vipList = uni.getStorageSync('vipList');
		if (vipList) {
			this.vipList = vipList;
		}

		this.getVipList();
		this.ovoUserSelf = this.$ovo.getSelf();
		uni.$on("changeOvoSelfInfo", () => {
			this.ovoUserSelf = this.$ovo.getSelf();
		})
	},
	mounted() {
	},
	methods: {
		toWebview(src) {
			this.$kt.util.toWebview(src);
		},

		toSelectedOfItem(item) {
			if (this.selectedOfItem && this.selectedOfItem.code == item.code) {
				this.selectedOfItem = null;
				return;
			}
			this.selectedOfItem = item;
		},
		onChooseAvatar(e) {
			console.log(e);
		},
		getVipList() {
			this.$kt.request.request({
				api: this.$api.business.ovo.vip.getAll,
				stateSuccess: (res) => {
					this.vipList = res.data;
					// 存入缓存
					uni.setStorageSync('vipList', res.data);

				}
			})
		}
	}
}
</script>

